<template>
    <div class="find-difference-game">
        <div class="game-container">
            <div class="game-header">
                <h2 class="game-title">🔍 关卡一：火眼金睛挑战</h2>
                <p class="game-subtitle">
                    36张图片中，有35张是相同的，只有1张不一样
                </p>
                <p class="game-hint">👀 找出那张独一无二的图片！</p>
            </div>

            <div class="image-grid">
                <div
                    v-for="(image, index) in imageGrid"
                    :key="index"
                    class="image-item"
                    :class="{ selected: selectedIndex === index }"
                    @click="handleImageClick(index)"
                    @mouseenter="hoveredIndex = index"
                    @mouseleave="hoveredIndex = null"
                >
                    <img :src="image.url" :alt="`图片 ${index + 1}`" />
                    <div
                        v-if="showResult && index === differentIndex"
                        class="correct-indicator"
                    >
                        ✓
                    </div>
                </div>
            </div>

            <div v-if="message" class="message" :class="messageType">
                {{ message }}
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted } from "vue";

const emit = defineEmits(["success"]);

// 导入所有图片
const availableImages = [
    new URL("../assets/images/20251024-142306.jpg", import.meta.url).href,
    new URL("../assets/images/20251024-142310.jpg", import.meta.url).href,
    new URL("../assets/images/20251024-142313.jpg", import.meta.url).href,
];

const imageGrid = ref([]);
const differentIndex = ref(-1);
const selectedIndex = ref(null);
const hoveredIndex = ref(null);
const message = ref("");
const messageType = ref("");
const showResult = ref(false);

// 初始化游戏
const initGame = () => {
    // 从所有图片中随机选择两张不同的图片
    const shuffled = [...availableImages].sort(() => Math.random() - 0.5);
    const imageA = shuffled[0]; // 这张显示35次
    const imageB = shuffled[1]; // 这张显示1次（正确答案）

    // 创建6x6的网格（36个格子）
    // 先填充35个相同的图片
    const grid = [];
    for (let i = 0; i < 35; i++) {
        grid.push({ url: imageA, isDifferent: false });
    }

    // 随机选择一个位置（0-35）插入唯一的图片（正确答案）
    const randomIndex = Math.floor(Math.random() * 36);
    grid.splice(randomIndex, 0, { url: imageB, isDifferent: true });

    differentIndex.value = randomIndex;
    imageGrid.value = grid;
    message.value = "";
    messageType.value = "";
    selectedIndex.value = null;
    showResult.value = false;
};

// 处理图片点击
const handleImageClick = (index) => {
    if (showResult.value) return; // 已经显示结果，不再响应点击

    selectedIndex.value = index;

    if (imageGrid.value[index].isDifferent) {
        // 答对了 - 找到了只出现1次的图片
        message.value = "🎉 恭喜！找到了那张独一无二的图片！";
        messageType.value = "success";
        showResult.value = true;

        // 1秒后进入下一关
        setTimeout(() => {
            emit("success");
        }, 1000);
    } else {
        // 答错了 - 这张图片出现了35次，不是答案
        message.value = "❌ 这张图片出现了很多次哦，找找那张只有1次的～";
        messageType.value = "error";

        // 1秒后清除错误提示
        setTimeout(() => {
            message.value = "";
            messageType.value = "";
            selectedIndex.value = null;
        }, 1000);
    }
};

onMounted(() => {
    initGame();
});
</script>

