<template>
    <div class="quiz-game">
        <div class="game-container">
            <div class="game-header">
                <h2 class="game-title">🎯 关卡二：同事八卦问答</h2>
                <p class="game-subtitle">答对问题才能输入密码哦～</p>
                <div class="progress-bar">
                    <div class="progress-text">
                        第 {{ currentQuestionIndex + 1 }} 题 / 共 4 题
                    </div>
                    <div class="progress-track">
                        <div
                            class="progress-fill"
                            :style="{
                                width: `${((currentQuestionIndex + 1) / 4) * 100}%`,
                            }"
                        ></div>
                    </div>
                </div>
            </div>

            <!-- 答题阶段 -->
            <div v-if="!quizCompleted" class="quiz-section">
                <div class="question-card">
                    <div class="question-number">
                        问题 {{ currentQuestionIndex + 1 }}
                    </div>
                    <div class="question-text">
                        {{ currentQuestion.question }}
                    </div>

                    <div class="options-list">
                        <button
                            v-for="(option, index) in currentQuestion.options"
                            :key="index"
                            class="option-button"
                            :class="{
                                selected: selectedAnswer === option,
                                correct:
                                    showAnswer &&
                                    option === currentQuestion.answer,
                                wrong:
                                    showAnswer &&
                                    selectedAnswer === option &&
                                    option !== currentQuestion.answer,
                            }"
                            @click="selectAnswer(option)"
                            :disabled="showAnswer"
                        >
                            <span class="option-letter">{{
                                String.fromCharCode(65 + index)
                            }}</span>
                            <span class="option-text">{{ option }}</span>
                            <span
                                v-if="
                                    showAnswer &&
                                    option === currentQuestion.answer
                                "
                                class="result-icon"
                                >✓</span
                            >
                            <span
                                v-if="
                                    showAnswer &&
                                    selectedAnswer === option &&
                                    option !== currentQuestion.answer
                                "
                                class="result-icon"
                                >✗</span
                            >
                        </button>
                    </div>

                    <button
                        v-if="!showAnswer"
                        class="submit-button"
                        :disabled="!selectedAnswer"
                        @click="submitAnswer"
                    >
                        确认答案
                    </button>

                    <div v-if="showAnswer" class="answer-feedback">
                        <div v-if="isCorrect" class="feedback correct">
                            <span class="feedback-icon">🎉</span>
                            <span class="feedback-text"
                                >✓ 正确！你真了解TA！</span
                            >
                        </div>
                        <div v-else class="feedback wrong">
                            <span class="feedback-icon">💡</span>
                            <span class="feedback-text"
                                >✗ 错啦！正确答案是：{{
                                    currentQuestion.answer
                                }}</span
                            >
                        </div>
                    </div>
                </div>
            </div>

            <!-- 密码输入阶段 -->
            <div v-else class="password-section">
                <div class="unlock-status">
                    <p class="status-text">
                        已解锁 {{ correctCount }} / 4 个字符
                    </p>
                    <div class="unlock-icons">
                        <span
                            v-for="i in 4"
                            :key="i"
                            class="unlock-icon"
                            :class="{ unlocked: i <= correctCount }"
                        >
                            {{ i <= correctCount ? "🔓" : "🔒" }}
                        </span>
                    </div>
                </div>

                <div v-if="correctCount === 4" class="password-input-area">
                    <!-- 视频播放区域 -->
                    <div v-if="showVideo" class="video-container">
                        <video
                            ref="videoPlayer"
                            class="celebration-video"
                            controls
                            autoplay
                            @ended="handleVideoEnd"
                        >
                            <source :src="videoUrl" type="video/mp4" />
                            您的浏览器不支持视频播放。
                        </video>
                        <button class="skip-video-btn" @click="skipVideo">
                            跳过视频 ⏭️
                        </button>
                    </div>

                    <!-- 密码输入区域 -->
                    <div v-else>
                        <p class="input-hint">请输入任意4位密码：</p>
                        <p class="input-subtitle">
                            ✨ 随便输入什么都可以通过哦～
                        </p>
                        <div class="password-inputs">
                            <input
                                v-for="i in 4"
                                :key="i"
                                :ref="(el) => (passwordInputs[i - 1] = el)"
                                v-model="passwordChars[i - 1]"
                                type="text"
                                maxlength="1"
                                class="password-char-input"
                                @input="handlePasswordInput(i - 1)"
                                @keydown.backspace="handleBackspace(i - 1)"
                            />
                        </div>
                        <button
                            class="verify-button"
                            @click="verifyPassword"
                            :disabled="!isPasswordComplete"
                        >
                            验证密码
                        </button>

                        <div v-if="passwordError" class="password-error">
                            ❌ {{ passwordError }}
                        </div>
                    </div>
                </div>

                <div v-else class="retry-section">
                    <div class="retry-message">
                        <p>
                            😅 你只答对了
                            {{ correctCount }} 题，还不能输入完整密码哦～
                        </p>
                        <p class="retry-hint">
                            需要全部答对才能解锁4位密码输入！
                        </p>
                    </div>
                    <button class="retry-button" @click="retryQuiz">
                        🔄 重新挑战
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, computed, onMounted, nextTick } from "vue";

const emit = defineEmits(["success"]);

// 题库
const questionBank = [
    {
        question: "以下哪一个零食bingo在零食区看到了会优先拿走？",
        options: ["卤凤爪", "博士牛筋", "霸王丝", "番薯干"],
        answer: "卤凤爪",
    },
    {
        question: "泰山的泡泡玛特山最高的是哪个？",
        options: ["Labubu", "人民币边角料", "泰山"],
        answer: "泰山",
    },
    {
        question: "zero的本名是什么？",
        options: ["郑咏零", "郑之楼", "郑咏柔"],
        answer: "郑咏柔",
    },
    {
        question: "宏填的痣在脸的哪边？",
        options: ["左边", "右边"],
        answer: "右边",
    },
    {
        question: "军哥去KTV必点的歌是什么歌？",
        options: ["春天花会开", "我是一只鱼", "一千个伤心的理由"],
        answer: "我是一只鱼",
    },
];

// 游戏状态
const selectedQuestions = ref([]);
const currentQuestionIndex = ref(0);
const selectedAnswer = ref("");
const showAnswer = ref(false);
const isCorrect = ref(false);
const correctCount = ref(0);
const quizCompleted = ref(false);
const answerResults = ref([]);

// 密码输入
const passwordChars = ref(["", "", "", ""]);
const passwordInputs = ref([]);
const passwordError = ref("");
const showVideo = ref(false);
const videoPlayer = ref(null);

// 导入视频
const videoUrl = new URL(
    "../assets/video/飞书20251024-151425.mp4",
    import.meta.url,
).href;

const currentQuestion = computed(() => {
    return selectedQuestions.value[currentQuestionIndex.value] || {};
});

const isPasswordComplete = computed(() => {
    return passwordChars.value.every((char) => char.length === 1);
});

// 初始化游戏：随机抽取4道题
const initGame = () => {
    const shuffled = [...questionBank].sort(() => Math.random() - 0.5);
    selectedQuestions.value = shuffled.slice(0, 4);
    currentQuestionIndex.value = 0;
    selectedAnswer.value = "";
    showAnswer.value = false;
    isCorrect.value = false;
    correctCount.value = 0;
    quizCompleted.value = false;
    answerResults.value = [];
    passwordChars.value = ["", "", "", ""];
    passwordError.value = "";
    showVideo.value = false;
};

// 选择答案
const selectAnswer = (option) => {
    if (!showAnswer.value) {
        selectedAnswer.value = option;
    }
};

// 提交答案
const submitAnswer = () => {
    if (!selectedAnswer.value) return;

    showAnswer.value = true;
    isCorrect.value = selectedAnswer.value === currentQuestion.value.answer;

    if (isCorrect.value) {
        correctCount.value++;
    }

    answerResults.value.push({
        question: currentQuestion.value.question,
        correct: isCorrect.value,
        userAnswer: selectedAnswer.value,
        correctAnswer: currentQuestion.value.answer,
    });

    // 3秒后自动进入下一题或完成
    setTimeout(() => {
        if (currentQuestionIndex.value < selectedQuestions.value.length - 1) {
            nextQuestion();
        } else {
            completeQuiz();
        }
    }, 3000);
};

// 下一题
const nextQuestion = () => {
    currentQuestionIndex.value++;
    selectedAnswer.value = "";
    showAnswer.value = false;
    isCorrect.value = false;
};

// 完成答题
const completeQuiz = () => {
    quizCompleted.value = true;

    // 如果全对，自动聚焦到第一个密码输入框
    if (correctCount.value === 4) {
        nextTick(() => {
            if (passwordInputs.value[0]) {
                passwordInputs.value[0].focus();
            }
        });
    }
};

// 重新挑战
const retryQuiz = () => {
    showVideo.value = false;
    if (videoPlayer.value) {
        videoPlayer.value.pause();
        videoPlayer.value.currentTime = 0;
    }
    initGame();
};

// 处理密码输入
const handlePasswordInput = (index) => {
    // 限制只能输入一个字符
    if (passwordChars.value[index].length > 1) {
        passwordChars.value[index] = passwordChars.value[index].slice(0, 1);
    }

    // 自动聚焦到下一个输入框
    if (passwordChars.value[index].length === 1 && index < 3) {
        nextTick(() => {
            if (passwordInputs.value[index + 1]) {
                passwordInputs.value[index + 1].focus();
            }
        });
    }

    passwordError.value = "";
};

// 处理退格键
const handleBackspace = (index) => {
    if (passwordChars.value[index] === "" && index > 0) {
        nextTick(() => {
            if (passwordInputs.value[index - 1]) {
                passwordInputs.value[index - 1].focus();
            }
        });
    }
};

// 验证密码 - 任何密码都能通过
const verifyPassword = () => {
    const enteredPassword = passwordChars.value.join("");

    // 任何4位密码都能通过，播放视频
    if (enteredPassword.length === 4) {
        showVideo.value = true;
        // 确保视频元素加载后再播放
        nextTick(() => {
            if (videoPlayer.value) {
                videoPlayer.value.play().catch((err) => {
                    console.log("视频自动播放失败:", err);
                });
            }
        });
    } else {
        passwordError.value = "请输入4位密码";
    }
};

// 视频播放结束
const handleVideoEnd = () => {
    // 视频播放完成后，进入成功页面
    emit("success", {
        correctCount: correctCount.value,
        totalQuestions: 4,
        results: answerResults.value,
    });
};

// 跳过视频
const skipVideo = () => {
    if (videoPlayer.value) {
        videoPlayer.value.pause();
    }
    emit("success", {
        correctCount: correctCount.value,
        totalQuestions: 4,
        results: answerResults.value,
    });
};

onMounted(() => {
    initGame();
});
</script>

