<template>
    <div class="happy-verification">
        <!-- 关卡一：火眼金睛挑战 -->
        <FindDifferenceGame
            v-if="currentStage === 1"
            @success="handleStage1Success"
        />

        <!-- 关卡二：同事八卦问答 -->
        <QuizGame v-if="currentStage === 2" @success="handleStage2Success" />

        <!-- 成功页面 -->
        <div v-if="currentStage === 3" class="success-page">
            <div class="success-container">
                <div class="success-animation">
                    <div class="celebration-emoji">🎉</div>
                    <h1 class="success-title">恭喜通关！</h1>
                    <p class="success-subtitle">欢迎回来～</p>
                </div>

                <div class="stats-section">
                    <h3 class="stats-title">你的成绩单</h3>
                    <div class="stats-grid">
                        <div class="stat-item">
                            <div class="stat-icon">🎯</div>
                            <div class="stat-label">答题准确率</div>
                            <div class="stat-value">{{ accuracy }}%</div>
                        </div>
                        <div class="stat-item">
                            <div class="stat-icon">✅</div>
                            <div class="stat-label">答对题数</div>
                            <div class="stat-value">{{ correctCount }}/4</div>
                        </div>
                        <div class="stat-item">
                            <div class="stat-icon">⏱️</div>
                            <div class="stat-label">通关时间</div>
                            <div class="stat-value">{{ completionTime }}秒</div>
                        </div>
                    </div>
                </div>

                <div class="message-section">
                    <p class="congrats-message">{{ congratsMessage }}</p>
                    <div class="fun-fact">
                        <span class="fun-fact-icon">💡</span>
                        <span>{{ randomFunFact }}</span>
                    </div>
                </div>

                <div class="action-section">
                    <button @click="restart" class="action-btn restart-btn">
                        🔄 重新挑战
                    </button>
                    <button @click="goToMain" class="action-btn main-btn">
                        🏠 进入系统
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import FindDifferenceGame from "./FindDifferenceGame.vue";
import QuizGame from "./QuizGame.vue";

const emit = defineEmits(["success"]);

const currentStage = ref(1);
const startTime = ref(0);
const endTime = ref(0);
const quizResults = ref(null);

const funFacts = [
    "据统计，平均每个人需要30秒才能找出不同的图片！",
    "恭喜你！你对同事的了解程度超过了80%的人！",
    '这个验证系统被评为"最温暖的反人类设计"😄',
    "你知道吗？答对的每一题都让团队更紧密一分！",
    "有趣的发现：经常答对问题的人，午餐时聊天更多！",
    "这个系统的设计灵感来自产品经理的深夜脑洞✨",
    '你已经成为"八卦达人"候选人之一！',
    "每次登录都是一次团建，感受到了吗？",
];

const congratsMessages = [
    "你真是团队里的细节观察家！👀",
    "火眼金睛+八卦达人=完美通关！🎊",
    "你对同事的了解让人感动！❤️",
    "团队凝聚力+1，全靠你的努力！🤝",
    "观察力MAX，了解度MAX！💯",
];

const randomFunFact = ref("");
const congratsMessage = ref("");

const correctCount = computed(() => {
    return quizResults.value?.correctCount || 0;
});

const accuracy = computed(() => {
    if (!quizResults.value) return 0;
    return Math.round((quizResults.value.correctCount / 4) * 100);
});

const completionTime = computed(() => {
    if (endTime.value && startTime.value) {
        return Math.round((endTime.value - startTime.value) / 1000);
    }
    return 0;
});

const handleStage1Success = () => {
    console.log("关卡一通关！");
    currentStage.value = 2;
};

const handleStage2Success = (data) => {
    console.log("关卡二通关！", data);
    quizResults.value = data;
    endTime.value = Date.now();
    currentStage.value = 3;

    // 随机选择祝贺语和趣味事实
    congratsMessage.value =
        congratsMessages[Math.floor(Math.random() * congratsMessages.length)];
    randomFunFact.value = funFacts[Math.floor(Math.random() * funFacts.length)];

    // 触发成功事件
    emit("success", {
        correctCount: correctCount.value,
        accuracy: accuracy.value,
        completionTime: completionTime.value,
    });
};

const restart = () => {
    currentStage.value = 1;
    startTime.value = Date.now();
    endTime.value = 0;
    quizResults.value = null;
};

const goToMain = () => {
    // 这里可以跳转到主系统或触发父组件的事件
    emit("success", {
        correctCount: correctCount.value,
        accuracy: accuracy.value,
        completionTime: completionTime.value,
    });
};

onMounted(() => {
    startTime.value = Date.now();
});
</script>

